import { CreateCompanyDto } from "../types/dtos/company.dto";
import { RepresentativeForm, convertRepresentativeFormToDto } from "./person.forms";

export type CompanyForm = {
  name: string;
  taxId: string;
  headquarters: string;
  phone: string;
  email: string;
  representatives: RepresentativeForm[];
};

export const getDefaultCompanyForm = (): CompanyForm => ({
  name: '',
  taxId: '',
  headquarters: '',
  phone: '',
  email: '',
  representatives: [] as RepresentativeForm[]
});

export const convertCompanyFormToDto = (form: CompanyForm): CreateCompanyDto => ({
  name: form.name.trim(),
  taxId: form.taxId.trim() || null,
  headquarters: form.headquarters.trim() || null,
  phone: form.phone.trim() || null,
  email: form.email.trim() || null,
  representatives: form.representatives.map(convertRepresentativeFormToDto)
});